:root {
  --main-accent-color: #dadfe0;

}

*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body, #root {
  width: 100vw;
  height: 100vh;
}

.gpx-submit {
  background-color: #c4ce6d;
  border-style: solid;
  border-color: black;
  border-radius: 50px;
  height: 20%;
  width: 50%;
  position: absolute;
  top: 50%;
  cursor: pointer;
  transition: 0.3s;
}

.gpx-submit:hover {
  background: #c7c0c0;
}

.gpx-form input[type="text"] {
  height: clamp(20px, 30%, 50px);
  width: 70%;
  border-radius: 20px;
  border-style: solid;
  position: absolute;
}

.gpx-form {
  width: clamp(200px, 50vw, 300px);
  height: clamp(150px, 30vh, 300px);
  border-style: solid;
  border-radius: 1px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50vw;
  top: 30vh;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
}

.quest-select-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: clamp(10rem, 30vw, 30rem);
  height: clamp(5rem, 20vh, 30rem);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.select-quest,  .button-quest {
  cursor: pointer;
  width: fit-content;
  height: clamp(2rem, 3vh, 5rem);
  border: 1px solid black;
  border-radius: 0.4rem;
  background-color: var(--main-accent-color);
  border-color: #17252e;
  transition: 0.2s ease-in-out;
  text-align: center;
}

.select-quest .city,  .button-quest .city {
  margin-right: 1rem;
}

.button-quest {
  width: fit-content;
  padding-inline: 1rem;
  height: clamp(1rem, 10vh, 2rem);
}
.button-quest:hover
{
  background-color: #94d4b1;
  font-weight: 400;
}

.option-url {
  transition: 0.3s ease-in-out;
}

@media only screen and (max-width: 768px) and (max-height: 1024px)
{
  .quest-select-container
  {
    left: 0;
    transform: translateX(50%);
  }

  .gpx-form
  {
    margin-top: 5rem;
    margin: 0 auto;
  }
}